import React, { useState } from 'react';




const Team = () => {
    const [click, setClick] = useState(false)
    const handleClick = () => {
        console.log(click)
        setClick(!click)
    }
    const selectCity = [
        {
            city: 'Hà Nội',
            district: 'Hai Bà Trưng',
            Ward: 'Phố Huế'
        },
        {
            city: 'Hồ Chí Minh',
            district: 'Quận 1',
            Ward: 'Bình Thạnh'
        },
        {
            city: 'Huế',
            district: 'Trần quốc',
            Ward: 'Tân Bình'
        },
    ]
    return (
        <>
            <div className='wrapperButton'>
                <button className='cancle'>Hủy</button>
                <button className='create'>Tạo</button>
            </div>

            <h1 className='topInfor'>Thông tin chung</h1>
            <form className='formTeam'>
                <div className='divForm'>
                    <label className='nameLable'>Họ tên</label>
                    <input placeholder='Nhập họ tên khách hàng'></input>
                </div>
                
                <div className='divForm'>
                    <label>Điện thoại</label>
                    <input placeholder='Nhập số điện thoại'></input>
                </div>
                <div className='divForm'>
                    <label>Email</label>
                    <input placeholder='Nhập email'></input>
                </div>
            
               
                <div className='divForm'>
                    <label>Ngày sinh</label>
                    <input type='Date'></input>
                </div>
                <div className='divForm' >
                    <label>Giới tính</label>
                    <label class="switch" >
                        <input type="checkbox" onClick={handleClick}></input>
                        <span class="slider round"></span>
                    </label>
                    <label className='textForm'>{click === false ? "Nam" : "Nữ"}</label>
                </div>
                <div className='divForm'>
                    <label>Địa chỉ</label>
                    <input placeholder='Địa chỉ'></input>
                </div>
                <div className='divForm'>
                    <div className='divForm1'></div>
                    <div className='divForm2'>
                        <select className='selectClass'>
                            <option>
                                Tỉnh/Thành phố*
                            </option>
                            {selectCity.map((item) => <option>{item.city}</option>)}
                        </select>
                        <select className='selectClass'>
                            <option>Quận/Huyện*</option>
                            {selectCity.map((item) => <option>{item.district}</option>)}
                        </select>
                        <select className='selectClass'>
                            <option>Xã/Phường*</option>
                            {selectCity.map((item) => <option>{item.Ward}</option>)}
                        </select>
                    </div>
                </div>

            </form>
            <h1 className='topInfor'>Nhu cầu</h1>
            <textarea></textarea>
        </>
    )
}
export default Team